.movie-list{
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(4,minmax(200px,1fr));
    width: 100%;
}

@media screen and (max-width:1000px) {
    .movie-list{
        grid-template-columns: repeat(2,1fr);
    }
}
@media screen and (max-width:600px) {
    .movie-list{
        grid-template-columns: 1fr;
        width: 80%;
    }
}
