nav{
    width: 15%;
    height: 100vh;
    overflow: hidden;
}

nav > h1{
    width: 15%;
    position: fixed;
    top: 50px;
    text-align: center;
}
nav > ul{
    box-sizing: border-box;
    position: fixed;
    top: 100px;
    left: 20px;
    background-color: #fff;
    font-weight: bolder;
}
nav > ul > li{
    padding: 10px 20px;
}
nav > ul > li:nth-child(2){
    background-color: bisque;
    box-shadow: 0px 0px 17px 7px rgba(0,0,0,0.09);
    border-radius: 5px;
}
