.movie-item{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    background-color: #fff;
    box-shadow: 0px 0px 17px 7px rgba(0,0,0,0.09);
    border-radius: 5px;
    margin: 20px;
    overflow: hidden;
}
.movie-item img{
    width: 100%;
}
.movie-item h3{
    margin: 10px;
    text-decoration: none;
}
.movie-item p{
    margin: 10px;
}